* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  margin: 0;
  background-color: rgb(26, 25, 29);
  display: flex;
  align-items: center;
  flex-direction: column;
}


button {
  width: 100px;
  height: 30px;
  border-radius: 20px;
  background-color: rgb(177, 99, 3);
  border: none;
  outline: none;
  cursor: pointer;
}
.error {
  z-index: 100;
  width: 500px;
  height: 250px;
  text-align: center;
  color: red;
  padding: 25px;
  border-radius: 15px;
  color: white;
  background: rgba(0, 0, 0, 0.67);
  overflow: hidden;
}

.back {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.form {
  background-color: rgb(26, 25, 29);
  display: flex;
  flex-direction: column;
  padding: 15px;
  width: 510px;
  height: 200px;
  border-radius: 15px;
  margin-top: 100px;
  grid-gap: 15px;
  gap: 15px;
  position: relative;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.input {
  display: flex;
  flex-direction: column;
  grid-gap: 6px;
  gap: 6px;
}



.form input {
  width: 480px;
  height: 60px;
  outline: none;
  background: transparent;
  border: 2px solid rgb(26, 25, 29);
  font-family: inherit;
  border-bottom: 2px solid #2e2a33;
  color: rgba(255, 255, 255, 0.96);
  border-radius: 15px;
  font-size: 25px;
  padding-left: 17px;
  padding-top: 20px;
  color: rgba(255, 255, 255, 0.5);
}
.input > label[id="username"] {
  position: absolute;
  pointer-events: none;
  translate: 0 -50%;
  color: rgba(255, 255, 255, 0.5);
  transition: 2s;
}
.input > label[id="age"] {
  position: absolute;
  pointer-events: none;
  translate: 0 -50%;
  color: rgba(255, 255, 255, 0.5);
  transition: 2s;
}

.input > label[id="username"] {
  top: 29%;
  left: 34px;
}
.input > label[id="age"] {
  top: 65%;
  left: 34px;
}

.input > :is(input[id="username"]:focus, .has-value) ~ label[id="username"] {
  color: #8f44fd;
  translate: 0 -40px;
  transition: 0.5s;
}
.input > :is(input[id="age"]:focus, .has-value) ~ label[id="age"] {
  color: #8f44fd;
  translate: 0 -40px;
  transition: 0.5s;
}

input[id="age"]:focus{
    border-bottom-color: #8f44fd;
}
input[id="username"]:focus{
    border-bottom-color: #8f44fd;
}

.wErr{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.allUsers {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  width: 510px;
  border-radius: 15px;
  align-items: center;
  padding: 15px;
  grid-gap: 8px;
  gap: 8px;
}
.all {
  border-radius: 20px;
  background-color: #2e2a33;
  grid-gap: 10px;
  gap: 10px;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}

.userList {
  margin-left: 15px;
  margin-right: 15px;
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  width: 480pxx;
  padding: 10px;
  color: rgba(255, 255, 255, 0.184);
}

#user:hover {
  color: #8f44fd;
  transition: all 0.2s ease-in;
}

#underline {
  height: 3px;
  visibility: hidden;
}

#user:hover ~ #underline {
  background-color: #8f44fd;
  width: auto;
  visibility: visible;
  border-radius: 20px;
  -webkit-animation: reveal 0.7s;
          animation: reveal 0.7s;
}
@-webkit-keyframes reveal {
  from {
    -webkit-clip-path: inset(0 100% 0);
            clip-path: inset(0 100% 0);
  }
  to {
    -webkit-clip-path: inset(0 0 0);
            clip-path: inset(0 0 0);
  }
}
@keyframes reveal {
  from {
    -webkit-clip-path: inset(0 100% 0);
            clip-path: inset(0 100% 0);
  }
  to {
    -webkit-clip-path: inset(0 0 0);
            clip-path: inset(0 0 0);
  }
}

i {
  cursor: pointer;
}

